import {Button, Modal, RangeCalendar} from '@ui-kitten/components';
import {View} from 'react-native';
import styles from '../../styles';
import TextCM from '../../../../components/Text';
import {useTranslation} from 'react-i18next';

type Props = {
  onClose: () => void;
  visible: boolean;
  rangeDate: {};
  onConfirm: (rangeDate: {}) => void;
};

const ModalPickerDate = ({onClose, visible, rangeDate, onConfirm}: Props) => {
  const {t} = useTranslation();

  return (
    <Modal
      backdropStyle={styles.backdrop}
      onBackdropPress={onClose}
      visible={visible}>
      <RangeCalendar
        style={{backgroundColor: '#fff'}}
        range={rangeDate}
        onSelect={nextRange => onConfirm(nextRange)}
        min={new Date(1900, 0, 0)}
        max={new Date()}
        renderFooter={() => (
          <View
            style={{
              alignItems: 'center',
              justifyContent: 'flex-end',
              flexDirection: 'row',
              columnGap: 6,
              marginRight: 16,
              marginTop: 16,
            }}>
            <Button
              onPress={() => onConfirm({})}
              size="small"
              status="info"
              appearance="outline"
              children={props => <TextCM {...props}>{t('CM.undo')}</TextCM>}
            />
            <Button
              onPress={onClose}
              size="small"
              appearance="outline"
              status="primary"
              children={props => <TextCM {...props}>{t('CM.select')}</TextCM>}
            />
          </View>
        )}
      />
    </Modal>
  );
};

export default ModalPickerDate;
