import {
  CalendarRange,
  Divider,
  Icon,
  Input,
  Layout,
  List,
  ListItem,
} from '@ui-kitten/components';
import {SafeAreaView, useSafeAreaInsets} from 'react-native-safe-area-context';
import {
  Keyboard,
  ScrollView,
  StatusBar,
  TouchableOpacity,
  TouchableWithoutFeedback,
  useWindowDimensions,
  View,
} from 'react-native';
import {useEffect, useRef, useState} from 'react';
import {useNavigation, useRoute} from '@react-navigation/native';
import {Color, Font, NavigationName} from '../../../constants';
import {IServices, ITicket} from '../../../model/interface/service.interface';
import {useAppSelector} from '../../../hooks/useRedux';
import {debounce, isEmpty} from 'lodash';
import {dataListStatusTicket} from '../my-ticket/helper';
import {useTranslation} from 'react-i18next';
import BottomSheetCM from '../../components/BottomSheet';
import TextCM from '../../components/Text';
import {IDropDown} from '../../components/DropDownList';
import {
  dataPrioritySelect,
  dataStatus,
} from '../home/component/AddTicketRelative/helper';
import {listFilter} from './helper';
import {Propity, Status} from '../../../model/enum/common.enum';
import ModalPickerDate from './components/ModalDate';
import {IListModelerByService} from './interface';
import BottomSheetFilter from './components/BottomShetFilter';
import {dataListStatusTicketFollow} from '../ticket-follow/helper';
import {DateUtils} from '../../../utils';
import styles from './styles';
import {TYPE_SCREEN} from '../../../constants/Size';
import {useTicketServices} from '../../../hooks/apiHooks/useTicketServices';
import React from 'react';

const SearchTicketScreen = () => {
  const {t} = useTranslation();
  const router = useRoute<any>();
  const searchFrom = router?.params?.type;
  const navigation = useNavigation<any>();
  const inset = useSafeAreaInsets();
  const inputRef = useRef<any>(null);
  const [listDataTicket, setListDataTicket] = useState<ITicket[]>([]);
  const [value, setValue] = useState('');
  const [totalRecoders, setTotalRecoders] = useState<number>(0);
  const [totalPages, setTotalPages] = useState<number>(0);
  const [pageModeler, setPageModeler] = useState<number>(0);
  const [page, setPage] = useState<number>(0);
  const [reloading, setReloading] = useState<boolean>(false);
  const {dataAccount} = useAppSelector(state => state.account);
  const {accessToken} = useAppSelector(state => state.common);

  const [debounceSearchText, setDebounceSearch] = useState<string>('');
  const ListServices = useTicketServices();
  const debouncedSearch = debounce((text: string) => {
    setDebounceSearch(text);
  }, 300);
  const [isVisibleSelect, setIsVisibleSelect] = useState<string>('');
  const [groupService, setGroupService] = useState<string[]>([]);
  const [groupStatusTicket, setGroupStatusTicket] = useState<string[]>([]);
  const [groupServices, setGroupServices] = useState<IDropDown[]>([]);
  const [groupModelerSelect, setGroupModelerSelect] = useState<string[]>([]);
  const [groupModeler, setGroupModeler] = useState<IDropDown[]>([]);
  const [priority, setPriority] = useState<Propity[]>([]);
  const [rangeDate, setRangeDate] = useState<CalendarRange<Date>>({});
  const [rangeEstimateDate, setRangeEstimateDate] = useState<
    CalendarRange<Date>
  >({});
  const [rangeDelayDate, setRangeDelayDate] = useState<CalendarRange<Date>>({});

  const {width} = useWindowDimensions();

  useEffect(() => {
    getGroupService();
  }, []);

  useEffect(() => {
    setPageModeler(0);
    setGroupModelerSelect([]);
    setGroupModeler([]);
    getListModelerByListServiceId(0, groupService);
  }, [groupService]);

  useEffect(() => {
    debouncedSearch(value);
    return () => {
      debouncedSearch.cancel();
    };
  }, [value]);

  useEffect(() => {
    if (isClearData()) {
      setListDataTicket([]);
      setTotalRecoders(0);
      setPage(0);
      return;
    }
    if (searchFrom === 'my-ticket') {
      getTicketFromMyTicket(
        debounceSearchText,
        groupStatusTicket,
        priority,
        groupService,
        rangeDate,
        rangeDelayDate,
        rangeEstimateDate,
        groupModelerSelect,
      );
    } else {
      getTicketFromTicketFollow(
        debounceSearchText,
        groupStatusTicket,
        rangeDate,
        groupModelerSelect,
        groupService,
      );
    }
    setPage(0);
  }, [
    debounceSearchText,
    groupStatusTicket,
    priority,
    groupService,
    rangeDate,
    rangeDelayDate,
    rangeEstimateDate,
    groupModelerSelect,
  ]);

  const isClearData = () => {
    return (
      groupStatusTicket.length == 0 &&
      priority.length == 0 &&
      groupService.length == 0 &&
      Object.keys(rangeDate).length == 0 &&
      Object.keys(rangeEstimateDate).length == 0 &&
      Object.keys(rangeDelayDate).length == 0 &&
      groupModelerSelect.length == 0 &&
      !debounceSearchText
    );
  };

  const getTicketFromMyTicket = (
    search: string,
    groupStatusTicket: string[],
    priority: any[],
    service: string[],
    rangeDate: any,
    rangeDelayDate: any,
    rangeEstimateDate: any,
    groupModelerSelect: any[],
  ) => {
    if (
      groupStatusTicket.length > 0 ||
      priority.length > 0 ||
      service.length > 0 ||
      Object.keys(rangeDate).length > 0 ||
      Object.keys(rangeEstimateDate).length > 0 ||
      Object.keys(rangeDelayDate).length > 0 ||
      groupModelerSelect.length > 0 ||
      !isEmpty(search)
    ) {
      getListTicket(
        0,
        search,
        groupStatusTicket,
        priority,
        service,
        rangeDate,
        rangeDelayDate,
        rangeEstimateDate,
        groupModelerSelect,
      );
    } else {
      setListDataTicket([]);
    }
  };

  const getTicketFromTicketFollow = (
    search: string,
    groupStatusTicket: string[],
    rangeDate: any,
    groupModelerSelect: any[],
    service: string[],
  ) => {
    if (
      service.length > 0 ||
      groupStatusTicket.length > 0 ||
      Object.keys(rangeDate).length > 0 ||
      groupModelerSelect.length > 0 ||
      !isEmpty(search)
    ) {
      getListTicketFollow(
        0,
        search,
        groupStatusTicket,
        rangeDate,
        groupModelerSelect,
        service,
      );
    } else {
      setListDataTicket([]);
    }
  };

  const getGroupService = async () => {
    try {
      const data = await ListServices.getServiceMenu(
        page,
        1000000,
        '',
        accessToken,
      );
      const filterData = data.content.map((item: IServices) => {
        return {
          label: item.serviceName,
          value: item.id,
        };
      });
      setGroupServices(filterData);
    } catch (error) {}
  };

  const getListModelerByListServiceId = async (
    page: number,
    groupModelerSelect: string[],
  ) => {
    try {
      const params = {
        page: page,
        size: 1000,
        sort: 'createdDate,desc',
      };
      const body = {
        serviceId: groupModelerSelect || [],
      };
      const data = await ListServices.getListModelerByListServiceId(
        params,
        body,
      );
      if (data) {
        setTotalPages(data?.totalPages);
        const filterData = data?.content?.map((item: IListModelerByService) => {
          return {
            label: item.name,
            value: item.id,
          };
        });
        if (page === 0) {
          setGroupModeler(filterData);
        } else {
          setGroupModeler([...groupModeler, ...filterData]);
        }
      }
    } catch (error) {}
  };

  const onRefreshModeler = async (callback: any) => {
    setPageModeler(0);
    await getListModelerByListServiceId(0, groupModelerSelect);
    callback();
  };

  const getMoreDataModeler = async () => {
    if (pageModeler < totalPages) {
      await getListModelerByListServiceId(pageModeler + 1, groupModelerSelect);
      setPageModeler(prevPage => prevPage + 1);
    }
  };

  const getListTicket = async (
    page: number,
    search: string,
    groupStatusTicket: string[],
    priority: any[],
    service: string[],
    rangeDate: any,
    rangeDelayDate: any,
    rangeEstimateDate: any,
    groupModelerSelect: any[],
  ) => {
    try {
      const bodySubmit = {
        createdBy: dataAccount?.email ?? '',
        myTicket: true,
        textSearch: search,
        ticketStatus: groupStatusTicket || [],
        priority: priority || [],
        service: service || [],
        delayDateFrom: rangeDelayDate.startDate || null,
        delayDateTo: rangeDelayDate.endDate || null,
        endDate: rangeDate.endDate || null,
        estimateDateFrom: rangeEstimateDate.startDate || null,
        estimateDateTo: rangeEstimateDate.endDate || null,
        startDate: rangeDate.startDate || null,
        workflow: groupModelerSelect || [],
      };
      const data = await ListServices.getTicketList(
        page,
        50,
        'createdDate,desc',
        accessToken,
        bodySubmit,
      );

      if (page === 0) {
        setListDataTicket(data?.content);
      } else {
        setListDataTicket([...listDataTicket, ...data?.content]);
      }
      setTotalRecoders(Number(data?.page?.totalRecords));
    } catch (error) {
    } finally {
      setReloading(false);
    }
  };

  const getListTicketFollow = async (
    page: number,
    searchText: string,
    groupStatusTicket: string[],
    rangeDate: any,
    groupModelerSelect: any[],
    service: string[],
  ) => {
    try {
      const search = {
        keyWord: searchText,
        terms: {
          status:
            groupStatusTicket.length > 0
              ? groupStatusTicket
              : [
                  Status.processing,
                  Status.accepted,
                  Status.rejected,
                  Status.voided,
                ],
        },
        from: rangeDate.startDate || null,
        to: rangeDate.endDate || null,
        id: null,
        field: 'createdDate',
      };
      if (groupModelerSelect && groupModelerSelect.length > 0) {
        (search as any).terms.modelerId = groupModelerSelect;
      }
      if (service && service.length > 0) {
        (search as any).terms.serviceId = service;
      }

      const data = await ListServices.getTicketListFollow(
        page,
        50,
        'createdDate,desc',
        search,
        accessToken,
      );
      if (data) {
        if (page === 0) {
          setListDataTicket(data?.content);
        } else {
          setListDataTicket([...listDataTicket, ...data?.content]);
        }
        setTotalRecoders(Number(data?.page?.totalRecords));
      }
    } catch (error) {
    } finally {
      setReloading(false);
    }
  };

  const fetchNextPage = () => {
    if (listDataTicket.length < totalRecoders) {
      setPage(page + 1);
      if (searchFrom === 'my-ticket') {
        getListTicket(
          page + 1,
          debounceSearchText,
          groupStatusTicket,
          priority,
          groupService,
          rangeDate,
          rangeDelayDate,
          rangeEstimateDate,
          groupModelerSelect,
        );
      } else {
        getListTicketFollow(
          page + 1,
          debounceSearchText,
          groupStatusTicket,
          rangeDate,
          groupModelerSelect,
          groupService,
        );
      }
    }
  };

  const onRefresh = () => {
    setPage(0);
    if (searchFrom === 'my-ticket') {
      getListTicket(
        1,
        debounceSearchText,
        groupStatusTicket,
        priority,
        groupService,
        rangeDate,
        rangeDelayDate,
        rangeEstimateDate,
        groupModelerSelect,
      );
    } else {
      getListTicketFollow(
        1,
        debounceSearchText,
        groupStatusTicket,
        rangeDate,
        groupModelerSelect,
        groupService,
      );
    }
  };

  const renderItem = ({
    item,
    index,
  }: {
    item: ITicket;
    index: number;
  }): React.ReactElement => {
    const dataStatus =
      searchFrom === 'my-ticket'
        ? dataListStatusTicket.find(data => data.status === item.ticketStatus)
        : dataListStatusTicketFollow.find(data => data.status === item.status);
    return (
      <ListItem
        onPress={() => {
          navigation.navigate(NavigationName.BottomTabNavigationTicketDetail, {
            dataTicketId:
              searchFrom === 'my-ticket'
                ? item?.id
                : item?.resourceId?.substr(4),
            email: searchFrom === 'my-ticket' ? item.createdBy : item.owner,
            status:
              searchFrom === 'my-ticket' ? item.ticketStatus : item.status,
            selectTabBottom: searchFrom === 'my-ticket' ? 1 : 2, // 1: tab from my ticket, 2: tab from ticket following,
            recipientDetailsId: item.implementId,
            userTaskId: item.recipientId,
          });
        }}>
        <View style={styles.ctnMain}>
          <View style={styles.ctnBody}>
            <Icon
              width={30}
              height={30}
              fill={dataStatus?.color}
              name="credit-card-outline"
            />
            <View
              style={{
                flex: 1,
                gap: 4,
              }}>
              <TextCM
                style={{
                  fontFamily: Font.InterSemiBold600,
                  fontSize: 16,
                }}>
                {`${
                  searchFrom === 'my-ticket' ? item.title : item.resourceName
                }`}
              </TextCM>
              <TextCM>
                {`${
                  searchFrom === 'my-ticket'
                    ? `${t('SR.modeler')}: ${item.serviceName || '---'}`
                    : `${t('TK.step')}: ` + item.notionName
                }`}
              </TextCM>
              <View
                style={{
                  flexDirection: 'row',
                  alignItems: 'center',
                  justifyContent:
                    width >= TYPE_SCREEN.mobile
                      ? 'flex-start'
                      : 'space-between',
                  columnGap: width >= TYPE_SCREEN.mobile ? 20 : 0,
                }}>
                <TextCM
                  style={{
                    fontStyle: 'italic',
                    fontSize: 12,
                  }}>
                  {`${t('SR.created_at')}: ` +
                    DateUtils.convertDateToDDMMYYYY_H_M(item.createdDate)}
                </TextCM>
                <View
                  style={{
                    backgroundColor: dataStatus?.bgColor,
                    borderRadius: 4,
                    paddingHorizontal: 4,
                    paddingVertical: 2,
                    minWidth: 100,
                  }}>
                  <TextCM
                    style={{
                      color: dataStatus?.color,
                      fontSize: 12,
                      textAlign: 'center',
                    }}>
                    {t(
                      `TK.${
                        searchFrom === 'my-ticket'
                          ? item.ticketStatus
                          : item.status
                      }`,
                    )}
                  </TextCM>
                </View>
              </View>
            </View>
          </View>
          <View>
            <Icon width={20} height={20} name="chevron-right-outline" />
          </View>
        </View>
      </ListItem>
    );
  };

  const renderListFilterStatus = () => {
    const getDataStatus =
      searchFrom === 'my-ticket'
        ? dataStatus
        : [
            {label: 'TK.processing', value: Status.processing},
            {label: 'TK.completed', value: Status.accepted},
            {label: 'TK.voided', value: Status.voided},
            {label: 'TK.rejected', value: Status.rejected},
            {label: 'TK.overdue', value: Status.overdue},
          ];
    return (
      <>
        <BottomSheetFilter
          isi18n={true}
          data={getDataStatus}
          dataSelect={groupStatusTicket}
          setDataSelect={setGroupStatusTicket}
        />
      </>
    );
  };

  const getBorderColor = (value: string) => {
    switch (value) {
      case 'status':
        if (groupStatusTicket.length == 0) return Color.borderColor;
        return '#048EC7';
      case 'group':
        return groupService.length === 0 ? Color.borderColor : '#048EC7';
      case 'priority':
        if (priority.length == 0) return Color.borderColor;
        return '#048EC7';
      case 'modeler':
        if (groupModelerSelect.length == 0) return Color.borderColor;
        return '#048EC7';
      case 'created_at':
        return Object.keys(rangeDate).length > 0
          ? '#048EC7'
          : Color.borderColor;
      case 'due_date':
        return Object.keys(rangeEstimateDate).length > 0
          ? '#048EC7'
          : Color.borderColor;
      case 'late':
        return Object.keys(rangeDelayDate).length > 0
          ? '#048EC7'
          : Color.borderColor;
      default:
        return Color.borderColor;
    }
  };

  const getListFilter = () => {
    if (searchFrom === 'my-ticket') {
      return listFilter;
    } else
      return listFilter.filter(
        data =>
          data.value === 'status' ||
          data.value === 'created_at' ||
          data.value === 'group' ||
          data.value === 'modeler',
      );
  };

  const getLabelOfListFilter = (item: {label: string; value: string}) => {
    switch (item.value) {
      case 'created_at':
        return getDate(rangeDate, item.value);
      case 'due_date':
        return getDate(rangeEstimateDate, item.value);
      case 'late':
        return getDate(rangeDelayDate, item.value);
      case 'group':
        return getLabelOfGroup(groupService, item.value, item.value);
      case 'priority':
        return getLabel(priority, item.value);
      case 'modeler':
        return getLabelOfGroup(groupModelerSelect, item.value, item.value);
      case 'status':
        return getLabel(groupStatusTicket, item.value);
      default:
        return '';
    }
  };

  const getLabelOfGroup = (
    listSelect: any,
    defaultLabel: string,
    type: string,
  ) => {
    if (listSelect?.length === 0) return t(`SR.${defaultLabel}`);
    if (listSelect?.length === 1) {
      if (type === 'modeler') {
        return groupModeler?.find(modeler => modeler.value === listSelect[0])
          ?.label;
      } else {
        return groupServices?.find(service => service.value === listSelect[0])
          ?.label;
      }
    }
    if (listSelect?.length > 1)
      if (type === 'modeler') {
        return `${
          groupModeler.find(modeler => modeler.value === listSelect[0])?.label
        } + ${(listSelect.length - 1).toString()}`;
      } else {
        return `${
          groupServices?.find(service => service.value === listSelect[0])?.label
        } + ${(listSelect.length - 1).toString()}`;
      }
  };

  const getLabel = (listSelect: any, defaultLabel: string) => {
    if (listSelect?.length === 0) return t(`SR.${defaultLabel}`);
    if (listSelect?.length === 1) return t(`TK.${listSelect[0]}`);
    if (listSelect?.length > 1)
      return t(`TK.${listSelect[0]}`) + ` + ${listSelect?.length - 1}`;
  };

  const getDate = (rangeDate: any, defaultLabel: string) => {
    if (Object.keys(rangeDate).length === 0) return t(`SR.${defaultLabel}`);
    if (!rangeDate.endDate) {
      return `${DateUtils.convertDateToDDMMYYYY(rangeDate.startDate)} - now`;
    }
    return `${DateUtils.convertDateToDDMMYYYY(
      rangeDate.startDate,
    )} - ${DateUtils.convertDateToDDMMYYYY(rangeDate.endDate)}`;
  };

  return (
    <>
      <StatusBar
        barStyle={'dark-content'}
        backgroundColor={Color.Transparent}
      />
      <TouchableWithoutFeedback onPress={() => Keyboard.dismiss()}>
        <SafeAreaView
          edges={['top']}
          style={{backgroundColor: Color.BgPrimary, flex: 1}}>
          <>
            <Input
              style={{
                backgroundColor: Color.BgPrimary,
                borderWidth: 0,
              }}
              textStyle={{color: '#fff'}}
              placeholderTextColor={'#fff'}
              ref={inputRef}
              placeholder={t('SR.searchTicket')}
              value={value}
              status="basic"
              autoFocus
              allowFontScaling={false}
              onChangeText={nextValue => setValue(nextValue)}
              accessoryLeft={
                <TouchableOpacity onPress={() => navigation.goBack()}>
                  <Icon name="arrow-back" fill="#ffffff" />
                </TouchableOpacity>
              }
              accessoryRight={
                value ? (
                  <TouchableOpacity onPress={() => setValue('')}>
                    <Icon name="close-outline" fill="#ffffff" />
                  </TouchableOpacity>
                ) : undefined
              }
            />
            <Divider />
            <Layout
              style={{
                flex: 1,
                paddingBottom: inset.bottom,
                backgroundColor: '#fff',
              }}
              level="1">
              <View>
                <ScrollView
                  showsHorizontalScrollIndicator={false}
                  contentContainerStyle={{
                    columnGap: 10,
                    padding: 10,
                  }}
                  horizontal>
                  {getListFilter().map(item => (
                    <TouchableOpacity
                      onPress={() => {
                        setIsVisibleSelect(item.value);
                      }}
                      key={item.label}
                      style={[
                        {
                          borderColor: getBorderColor(item.value),
                        },
                        styles.ctnFilter,
                      ]}>
                      <TextCM>{getLabelOfListFilter(item)}</TextCM>
                      <Icon
                        width={16}
                        height={16}
                        name={
                          isVisibleSelect === item.value
                            ? 'chevron-up-outline'
                            : 'chevron-down-outline'
                        }
                      />
                    </TouchableOpacity>
                  ))}
                </ScrollView>
              </View>

              {!isClearData() && (
                <List
                  style={{backgroundColor: '#fff', flex: 1}}
                  showsVerticalScrollIndicator={false}
                  data={listDataTicket}
                  renderItem={renderItem}
                  refreshing={reloading}
                  onRefresh={onRefresh}
                  onEndReached={fetchNextPage}
                  onEndReachedThreshold={0.5}
                  ListEmptyComponent={() => (
                    <View style={{margin: 10, flex: 1}}>
                      <TextCM>{t('CM.emptyData')}</TextCM>
                    </View>
                  )}
                />
              )}
            </Layout>
          </>
        </SafeAreaView>
      </TouchableWithoutFeedback>

      <BottomSheetCM
        isVisible={isVisibleSelect === 'status'}
        title={t('SR.selectStatus')}
        onClose={() => setIsVisibleSelect('')}
        renderContent={renderListFilterStatus()}
      />

      <BottomSheetCM
        isVisible={isVisibleSelect === 'priority'}
        title={t('HM.priotitySelect')}
        onClose={() => setIsVisibleSelect('')}
        renderContent={
          <BottomSheetFilter
            isi18n={true}
            data={dataPrioritySelect}
            dataSelect={priority}
            setDataSelect={setPriority}
          />
        }
      />

      <BottomSheetCM
        isVisible={isVisibleSelect === 'group'}
        title={t('SR.selectGroup')}
        onClose={() => setIsVisibleSelect('')}
        renderContent={
          <BottomSheetFilter
            data={groupServices}
            dataSelect={groupService}
            setDataSelect={setGroupService}
          />
        }
      />

      {isVisibleSelect === 'modeler' && (
        <BottomSheetCM
          isVisible={isVisibleSelect === 'modeler'}
          title={t('SR.selectService')}
          onClose={() => setIsVisibleSelect('')}
          renderContent={
            <BottomSheetFilter
              data={groupModeler}
              dataSelect={groupModelerSelect}
              setDataSelect={setGroupModelerSelect}
              isSearch={true}
              placeholder={t('SR.selectService')}
              onRefresh={onRefreshModeler}
              getMore={getMoreDataModeler}
            />
          }
        />
      )}

      <ModalPickerDate
        onClose={() => setIsVisibleSelect('')}
        visible={isVisibleSelect === 'created_at'}
        onConfirm={setRangeDate}
        rangeDate={rangeDate}
      />

      <ModalPickerDate
        onClose={() => setIsVisibleSelect('')}
        visible={isVisibleSelect === 'due_date'}
        onConfirm={setRangeEstimateDate}
        rangeDate={rangeEstimateDate}
      />

      <ModalPickerDate
        onClose={() => setIsVisibleSelect('')}
        visible={isVisibleSelect === 'late'}
        onConfirm={setRangeDelayDate}
        rangeDate={rangeDelayDate}
      />
    </>
  );
};

export default SearchTicketScreen;
