import {Icon, Input, List, ListItem} from '@ui-kitten/components';
import {IDropDown} from '../../../../components/DropDownList';
import {useEffect, useState} from 'react';
import {Dimensions, TouchableOpacity} from 'react-native';
import useDebounce from '../../../../../hooks/useDebounce';
import TextCM from '../../../../components/Text';
import {useTranslation} from 'react-i18next';
type Props = {
  data: IDropDown[];
  dataSelect: any[];
  setDataSelect: (value: any[]) => void;
  isSearch?: boolean;
  onRefresh?: (callback: () => void) => void;
  getMore?: () => void;
  placeholder?: string;
  isi18n?: boolean;
};

const BottomSheetFilter = ({
  data,
  dataSelect,
  setDataSelect,
  isSearch = false,
  onRefresh,
  getMore,
  placeholder,
  isi18n = false,
}: Props) => {
  const {t} = useTranslation();
  const [refreshing, setIsRefreshing] = useState<boolean>(false);
  const [dataValue, setDataValue] = useState(data);
  const [value, setValue] = useState<string>('');
  const debouncedValue = useDebounce(value, 500);

  const [screenW, setScreenW] = useState({
    width: Dimensions.get('window').width,
    height: Dimensions.get('window').height,
  });

  useEffect(() => {
    const sub = Dimensions.addEventListener('change', ({window}) => {
      setScreenW({
        width: window.width,
        height: window.height,
      });
    });

    return () => sub.remove();
  }, []);

  useEffect(() => {
    filterValue(debouncedValue);
  }, [debouncedValue]);

  const filterValue = (debouncedValue: string) => {
    setDataValue(
      data.filter(item =>
        item.label.toLowerCase().includes(debouncedValue.toLowerCase()),
      ),
    );
  };

  const handleOnRefresh = async () => {
    if (!isSearch) return;
    setIsRefreshing(true);
    onRefresh?.(() => setIsRefreshing(false));
  };

  const getMoreData = () => {
    if (!isSearch) return;
    getMore?.();
  };

  return (
    <>
      {isSearch && (
        <Input
          allowFontScaling={false}
          value={value}
          onChangeText={setValue}
          placeholder={placeholder}
          style={{backgroundColor: '#fff'}}
          accessoryRight={
            value ? (
              <TouchableOpacity hitSlop={15} onPress={() => setValue('')}>
                <Icon name="close-outline" />
              </TouchableOpacity>
            ) : undefined
          }
        />
      )}
      <List
        showsVerticalScrollIndicator={false}
        style={{backgroundColor: '#fff', height: (screenW.height / 3) * 2}}
        data={dataValue}
        onEndReachedThreshold={0.5}
        onRefresh={handleOnRefresh}
        refreshing={refreshing}
        onEndReached={getMoreData}
        renderItem={({item}: {item: any}) => (
          <ListItem
            onPress={() => {
              if (!dataSelect.includes(item.value)) {
                setDataSelect([...dataSelect, item.value]);
              } else {
                setDataSelect(
                  dataSelect.filter(status => status !== item.value),
                );
              }
            }}
            title={props => (
              <TextCM {...props}>
                {isi18n ? t(`${item.label}`) : item.label}
              </TextCM>
            )}
            accessoryRight={
              dataSelect.includes(item.value) ? (
                <Icon
                  fill="#06B7E8"
                  width={14}
                  height={14}
                  name="checkmark-outline"
                />
              ) : undefined
            }
          />
        )}
      />
    </>
  );
};

export default BottomSheetFilter;
