//import liraries
import React, {useEffect, useState} from 'react';
import {
  Platform,
  ScrollView,
  Share,
  TouchableOpacity,
  View,
} from 'react-native';
import styles from './styles';
import {useAppSelector} from '../../../../../../hooks/useRedux';
import {IPostMessageUserTask, STATUS_TASK} from '../../../interface';
import TableCM from '../../../../../components/Table';
import {SizeDP} from '../../../../../../constants/Size';
import TextCM from '../../../../../components/Text';
import {
  IconDownload,
  IconTrash,
  IconUpload,
  IconView,
} from '../../../../../../assets/images';
import PagingCM from '../../../../../components/Paging';
import {useNavigation} from '@react-navigation/native';
import {CommonUtils, DateUtils} from '../../../../../../utils';
import Toast from 'react-native-toast-message';
import {
  IInfoFile,
  IInfoFileRelative,
} from '../../../../ticket-detail/components/TabView/components/Document/interface';
import NavigationName from '../../../../../../constants/NavigationName';
import {useTranslation} from 'react-i18next';
import {useReviewServices} from '../../../../../../hooks/apiHooks/useReviewTicketServices';
import RNFS, {DownloadFileOptions} from 'react-native-fs';
import ReactNativeBlobUtil from 'react-native-blob-util';
import ModalFullScreen from '../../../../../components/ModalFullScreen';
import ViewFileScreen from '../../../../ticket-detail/components/TabView/components/Document/ViewFileWebView';
import {getConfig} from '../../../../../../services/httpClient';
type Props = {
  dataUserTask: IPostMessageUserTask;
};
// create a component
const Document = ({dataUserTask}: Props) => {
  const pageSize = 3;
  const {t} = useTranslation();
  const navigation = useNavigation<any>();
  const [currentPage, setCurrentPage] = useState(1);
  const [totalItems, setTotalItems] = useState<number>(0);
  const [dataRelative, setDataRelative] = useState<any[]>([]);
  const [isViewFile, setIsViewFile] = useState<boolean>(false);
  const [urlViewFile, setUrlViewFile] = useState<string>('');
  const [viewNameFile, setViewNameFile] = useState<string>('');
  const {dataTicketRedux} = useAppSelector(state => state.ticket);
  const {accessToken} = useAppSelector(state => state.common);
  const {dataAccount} = useAppSelector(state => state.account);
  const ReviewTicketServices = useReviewServices();
  useEffect(() => {
    getFileByUserTask(currentPage);
  }, [currentPage]);

  const getFileByUserTask = async (page: number) => {
    try {
      const params = {
        page: page - 1,
        size: pageSize,
        sort: 'createdDate,desc',
      };
      const dataResp = await ReviewTicketServices.getListAttachmentsUserTask(
        dataTicketRedux.id,
        dataUserTask._userTaskId,
        params,
        accessToken,
      );
      if (dataResp) {
        let filterData = [];
        if (dataAccount?.orgIn?.includes('/1978/5742')) {
          filterData = dataResp?.data.filter((d: any) => {
            return !(d.resource == 'eform' && d.type == 'upload_img');
          });
        } else {
          filterData = dataResp?.data;
        }

        if (filterData?.length === 0) {
          if (page > 1) {
            setCurrentPage(page - 1);
          } else {
            setDataRelative([]);
            setTotalItems(0);
          }
          return;
        }
        setTotalItems(dataResp?.totalRecords);
        convertDataFileToTable(filterData);
      }
    } catch (error) {}
  };

  const getListFile = async () => {
    try {
      const dataFile = await ReviewTicketServices.getListFileReview(
        dataTicketRedux.id,
        dataUserTask._userTaskId,
        accessToken,
      );
    } catch (error) {}
  };

  const renderActionInfoFile = (
    isInfoFile = true,
    data: IInfoFile | IInfoFileRelative,
  ) => {
    return (
      <View style={styles.ctnAction}>
        <TouchableOpacity
          onPress={() => handleViewFile(isInfoFile, data)}
          hitSlop={SizeDP(15)}>
          <IconView width={SizeDP(16)} height={SizeDP(16)} />
        </TouchableOpacity>
        <TouchableOpacity
          onPress={() => handleDownload(data, isInfoFile)}
          hitSlop={SizeDP(15)}>
          <IconDownload width={SizeDP(16)} height={SizeDP(16)} />
        </TouchableOpacity>
        {!isInfoFile &&
          (data as IInfoFileRelative).allowDeleted &&
          dataUserTask._status === STATUS_TASK.TASK_SEARCH && (
            <TouchableOpacity
              onPress={() => handleClearFile(data?.id?.toString())}
              hitSlop={SizeDP(15)}>
              <IconTrash width={SizeDP(16)} height={SizeDP(16)} />
            </TouchableOpacity>
          )}
      </View>
    );
  };

  const handleViewFile = async (
    isInfoFile = true,
    data: IInfoFile | IInfoFileRelative,
  ) => {
    setViewNameFile((data as IInfoFileRelative).fileName?.split('.')?.[0]);
    try {
      Toast.show({
        type: 'info',
        text1: t('CM.info'),
        text2: t('TK.viewFile'),
      });
      const formData = new FormData();
      if (!isInfoFile && (data as any).resource === 'ewriter') {
        const base64String = await getFiles(
          (data as any).userTaskId,
          (data as any)?.fileRepositoryId,
          (data as any)?.fileName,
        );
        formData.append('file', base64String);
        const dataView = await ReviewTicketServices.viewFile(
          formData,
          accessToken,
        );
        if (dataView?.code == 1) {
          setIsViewFile(true);
          setUrlViewFile(dataView.message);
        } else {
          Toast.show({
            type: 'error',
            text1: t('CM.noti'),
            text2: t('CM.error'),
          });
        }
        return;
      }
      let keyName: string = (data as any).name;
      if (isInfoFile) {
        keyName = (data as IInfoFile).keyName;
      } else {
        keyName = (data as IInfoFileRelative).name;
      }
      let bucketName: string = data.bucketName;
      if (!data.bucketName) {
        const arr = (data as any)?.name?.split('.s3-hfx03.fptcloud.com:443/');
        keyName = arr[1];
        bucketName = arr[0].split('https://')[1];
      }
      formData.append('keyName', keyName);
      formData.append('bucketName', bucketName);
      formData.append('beanName', data.beanName);
      const dataView = await ReviewTicketServices.viewFile(
        formData,
        accessToken,
      );
      if (dataView?.code == 1) {
        setIsViewFile(true);
        setUrlViewFile(dataView.message);
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.info'),
          text2: t('CM.error'),
        });
      }
    } catch (error) {
      Toast.show({
        type: 'error',
        text1: t('CM.info'),
        text2: t('CM.error'),
      });
    }
  };

  const getFiles = async (
    userTaskId: string,
    fileId: any,
    fileName: string,
  ) => {
    try {
      const type = fileName?.split('.')?.[1];
      const path = `${ReactNativeBlobUtil.fs.dirs.DocumentDir}/${new Date()
        .getTime()
        .toString()}.${type}`;
      const dataBase64 = await ReactNativeBlobUtil.config({
        path: path,
      })
        .fetch(
          'GET',
          `${getConfig().BASE_URL}/services/erequest/api/tickets/${
            dataTicketRedux.id
          }/userTaskId/${userTaskId}/fileId/${fileId}`,
        )
        .then(res => {
          let status = res.info().status;
          if (status == 200) {
            let base64Str = res.path();
            return base64Str;
          } else return null;
        })
        .catch(() => null);
      const file = {
        uri: Platform.OS === 'ios' ? dataBase64 : 'file://' + dataBase64,
        name: `${new Date().getTime().toString()}.${type}`,
        type: CommonUtils.getMimeType(fileName),
      };
      return file;
    } catch (error) {}
  };

  const handleClearFile = async (idFile: string) => {
    try {
      const data = await ReviewTicketServices.deleteFileRelative(
        idFile,
        accessToken,
      );
      await getFileByUserTask(currentPage);
      Toast.show({
        type: 'success',
        text1: t('CM.info'),
        text2: t('TK.deleteFileSuccess'),
      });
    } catch (error) {
      console.log(error);

      Toast.show({
        type: 'error',
        text1: t('CM.info'),
        text2: t('TK.deleteFileErr'),
      });
    }
  };

  const handleDownload = async (
    data: IInfoFile | IInfoFileRelative,
    isInfoFile: boolean,
  ) => {
    let fileName: string = (data as any).fileName;
    if (isInfoFile) {
      fileName = (data as IInfoFile).keyName.replace('/', '_');
    } else {
      fileName = (data as IInfoFileRelative).fileName;
    }

    const toFile = `${
      Platform.OS === 'android'
        ? RNFS.DownloadDirectoryPath
        : RNFS.DocumentDirectoryPath
    }/${new Date().getTime()}_${fileName}`;

    if (isInfoFile) {
      // await downloadFileInfoForm(
      //   toFile,
      //   data.id,
      //   (data as IInfoFile)?.notionId,
      // );
    } else {
      if (!data.bucketName) {
        await downloadFileNotBucketName(toFile, data.name);
      } else {
        await downloadFileWithBucketName(toFile, data.id);
      }
    }
  };

  const downloadFileNotBucketName = async (toFile: string, fromUrl: string) => {
    const options: DownloadFileOptions = {
      fromUrl: fromUrl,
      toFile,
      background: true,
      begin: (res: any) => {
        console.log('Download has begun', res);
      },
      progress: (res: any) => {
        let percentage = (res.bytesWritten / res.contentLength) * 100;
        // setProgress(percentage);
      },
      connectionTimeout: 1000 * 5,
    };
    try {
      const result = await RNFS.downloadFile(options).promise;
      if (result.statusCode === 200) {
        if (Platform.OS === 'ios') {
          const dataShare = await Share.share({
            url: toFile,
          });
          if (dataShare.action == Share.dismissedAction) {
            Toast.show({
              type: 'error',
              text1: t('CM.noti'),
              text2: t('CM.downloadFail'),
            });
          } else {
            Toast.show({
              type: 'success',
              text1: t('CM.noti'),
              text2: t('CM.downloadSuccess'),
            });
          }
        } else {
          Toast.show({
            type: 'success',
            text1: t('CM.noti'),
            text2: t('CM.downloadSuccess'),
          });
        }
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.noti'),
          text2: t('CM.downloadFail'),
        });
      }
    } catch (err: any) {
      Toast.show({
        type: 'error',
        text1: t('CM.noti'),
        text2: t('CM.downloadFail'),
      });
    }
  };

  const downloadFileWithBucketName = async (toFile: string, id: any) => {
    try {
      Toast.show({
        type: 'info',
        text1: t('CM.noti'),
        text2: t('CM.pleaseWait'),
      });
      const dataBase64 = await ReactNativeBlobUtil.fetch(
        'GET',
        `${
          getConfig().BASE_URL
        }/services/erequest/api/attachments/download-file?attachmentId=${id}`,
      )
        .then(res => {
          let status = res.info().status;
          if (status == 200) {
            let base64Str = res.base64();
            return base64Str;
          } else return null;
        })
        .catch(() => null);
      if (dataBase64) {
        try {
          await RNFS.writeFile(toFile, dataBase64, 'base64');
        } catch (error) {
          console.log(error);
        }
        if (Platform.OS === 'ios') {
          const dataShare = await Share.share({
            url: toFile,
          });
          if (dataShare.action == Share.dismissedAction) {
            Toast.show({
              type: 'error',
              text1: t('CM.noti'),
              text2: t('CM.downloadFail'),
            });
          } else {
            Toast.show({
              type: 'success',
              text1: t('CM.noti'),
              text2: t('CM.downloadSuccess'),
            });
          }
        } else {
          Toast.show({
            type: 'success',
            text1: t('CM.noti'),
            text2: t('CM.downloadSuccess'),
          });
        }
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.noti'),
          text2: t('CM.downloadFail'),
        });
      }
    } catch (error) {
      Toast.show({
        type: 'error',
        text1: t('CM.noti'),
        text2: t('CM.downloadFail'),
      });
    }
  };

  const getFileName = (fileName: any) => {
    if (!fileName) return '';
    return fileName?.split('.')?.slice(0, -1)?.join('.');
  };

  const getFormat = (name: string, resource: any) => {
    if (name) {
      if (
        name.split('.').pop() == 'docx' &&
        resource === 'ewriter' &&
        dataAccount?.orgIn?.includes('/1978/5742')
      ) {
        return 'pdf';
      } else {
        return name.split('.').pop();
      }
    }
    return '';
  };

  const convertDataFileToTable = (data: IInfoFileRelative[]) => {
    const newData = data.map((infoFile: IInfoFileRelative, index: number) => ({
      STT: String((currentPage - 1) * pageSize + 1 + index),
      keyName: (
        <TextCM style={styles.txtText} numberOfLines={1}>
          {getFileName(infoFile?.fileName)}
        </TextCM>
      ),
      createdBy: infoFile.createdBy,
      documentSize: CommonUtils.getSize(infoFile.size),
      type: getFormat(infoFile?.fileName, (infoFile as any).resource),
      lastModifiedDate: DateUtils.convertDateToDDMMYYYY_H_M(
        infoFile.lastModifiedDate,
      ),
      action: renderActionInfoFile(false, infoFile),
    }));
    setDataRelative(newData);
  };

  const handleUploadFile = async () => {
    try {
      const file = await CommonUtils.handleGetFile();
      if (!file) return;

      const formData = new FormData();
      formData.append('uploadFile', file);
      formData.append(
        'attachmentJson',
        JSON.stringify({
          ticketId: dataTicketRedux?.id,
          userTaskId: dataUserTask._userTaskId,
        }),
      );
      const data = await ReviewTicketServices.uploadFileRelative(
        formData,
        accessToken,
      );
      if (data) {
        await getFileByUserTask(currentPage);
        Toast.show({
          type: 'success',
          text1: t('CM.info'),
          text2: t('TK.uploadFileSuccess'),
        });
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.info'),
          text2: t('TK.uploadFileErr'),
        });
      }
    } catch (error) {
      console.log(error);

      Toast.show({
        type: 'error',
        text1: t('CM.info'),
        text2: t('TK.uploadFileErr'),
      });
    }
  };

  return (
    <View style={styles.ctnMain}>
      <ScrollView style={styles.ctnBody}>
        <View style={styles.ctnForm}>
          <View style={styles.viewForm}>
            <TextCM style={styles.txtTitle}>{t('RV.document')}</TextCM>
            {dataUserTask._status === STATUS_TASK.TASK_SEARCH && (
              <TouchableOpacity onPress={handleUploadFile} hitSlop={SizeDP(15)}>
                <IconUpload width={SizeDP(16)} height={SizeDP(16)} />
              </TouchableOpacity>
            )}
          </View>
          <TableCM
            stickyLeft={true}
            stickyColumnLeftIndex={1}
            tableHeaders={[
              'STT',
              'keyName',
              'createdBy',
              'documentSize',
              'type',
              'lastModifiedDate',
              'action',
            ]}
            tableData={dataRelative}
          />
          {totalItems > 0 && (
            <PagingCM
              collectionSize={totalItems}
              currentPage={currentPage}
              pageSize={pageSize}
              onPageChange={currentPage => setCurrentPage(currentPage)}
            />
          )}
        </View>

        {/* <View style={styles.ctnForm}>
          <TextCM style={styles.txtTitle}>{t('TK.templateInfo')}</TextCM>
          <TableCM
            stickyLeft={true}
            stickyColumnLeftIndex={1}
            tableHeaders={[
              'STT',
              'keyName',
              'createdBy',
              'documentSize',
              'type',
              'lastModifiedDate',
              'action',
            ]}
            tableData={[]}
          />
        </View> */}
      </ScrollView>
      <ModalFullScreen
        isVisible={isViewFile}
        children={
          <ViewFileScreen
            url={urlViewFile}
            onClose={() => setIsViewFile(false)}
            name={viewNameFile}
          />
        }
      />
    </View>
  );
};

//make this component available to the app
export default Document;
